/* Yleinen layout */
html, body { min-height: 100%; overflow-y: auto; }
body { margin: 0; background:#eaf6eb; font-family: Arial, sans-serif; color:#1a1a1a; }

.demo-container { padding: 20px; }
.header { text-align:center; margin-bottom: 18px; }
.logo { width:80px; display:block; margin:0 auto 8px; }

/* Kolmen palstan asettelu */
.content {
  display:flex;
  gap: 28px;
  align-items: flex-start; /* ei keskity pystysuunnassa */
  max-width: 1200px;
  margin: 0 auto;
}

/* Vasen palsta – tiiviimpi + oma pystyscroll jolloin teksti ei jää ruudun alle */
.left {
  flex: 1;
  max-height: calc(100vh - 180px);
  overflow-y: auto;
  padding-right: 8px;
}
.left h3 { font-size: clamp(18px, 2vw, 22px); margin: 0 0 8px; }
.benefits { font-size: clamp(13px, 1.1vw, 16px); line-height: 1.45; margin:0; padding-left:18px; }
.benefits li { margin: 6px 0; word-break: break-word; }
/* sirompi skrolli */
.left::-webkit-scrollbar { width: 8px; }
.left::-webkit-scrollbar-thumb { background: #d1dfe0; border-radius: 8px; }
.left { scrollbar-width: thin; scrollbar-color: #d1dfe0 transparent; }

/* Keskipalsta (puhelinkehys + video) */
.middle { flex: 1; display:flex; justify-content:center; }
.phone-frame {
  background: #fff;
  padding: 12px;
  border-radius: 22px;
  box-shadow: 0 10px 22px rgba(0,0,0,.08);
  position: relative;
  width: min(360px, 90%);
}
.phone-header {
  font-weight: 700;
  text-align: center;
  margin-bottom: 8px;
  position: relative;
}

/* tila-badge */
.status-pill {
  position: absolute;
  right: 8px; top: 0;
  font-size: 12px; font-weight: 600;
  padding: 4px 10px; border-radius: 999px; color:#fff;
}
.status-live  { background:#27ae60; }
.status-meds  { background:#e74c3c; }
.status-drink { background:#3498db; }

.profile-pic {
  width: 100%;
  height: auto;
  border-radius: 16px;
  display:block;
  background:#f2f2f2;
}

/* virheilmoitukset */
.error-chip{
  position: absolute; left: 12px; top: 12px;
  background:#333; color:#fff; font-size:12px;
  padding: 4px 8px; border-radius: 14px; opacity:.9;
}
.error-text{ color:#c0392b; font-size:12px; margin-top:8px; text-align:center; }

/* Oikea palsta */
.right { flex: 1; min-width: 280px; }

/* kuvauskortti */
.panel-card{
  background:#fff; border-radius:16px; padding:14px;
  box-shadow: 0 8px 18px rgba(0,0,0,.06);
  margin: 10px 0;
}
.panel-card.compact .panel-title{ font-weight:700; margin-bottom:8px; }
.panel-list{ margin:0 0 8px; padding-left:18px; font-size:14px; line-height:1.45; }
.panel-list li{ margin:6px 0; }
.dot{ display:inline-block; width:9px; height:9px; border-radius:50%; margin-right:6px; position:relative; top:-1px; }
.dot-red{ background:#e74c3c; }
.dot-blue{ background:#3498db; }
.dot-purple{ background:#8e44ad; }
.dot-green{ background:#27ae60; }
.dot-gray{ background:#95a5a6; }

.panel-link{
  color:#2b65f8; cursor:pointer; position:relative; z-index:1; pointer-events:auto;
  font-weight:600; font-size:14px;
}

/* Napit */
.buttons { display:flex; flex-wrap: wrap; gap: 10px; }
.btn{
  border:none; border-radius:10px; color:#fff; cursor:pointer;
  padding:10px 14px; font-weight:600; box-shadow: 0 3px 8px rgba(0,0,0,.08);
}
.btn.red   { background:#e74c3c; }
.btn.blue  { background:#3498db; }
.btn.green { background:#27ae60; }
.btn.gray  { background:#7f8c8d; }
.btn.purple{ background:#7c4dff; }
.btn:hover { filter: brightness(.97); }

/* Modal (käyttöpaneeli) */
.modal-backdrop{
  position: fixed; inset: 0;
  background: rgba(0,0,0,.35);
  display:flex; align-items:center; justify-content:center;
  z-index: 9999;
}
.modal-card{
  width: min(820px, 92vw);
  background:#fff; border-radius:18px; padding:18px;
  box-shadow: 0 12px 24px rgba(0,0,0,.18);
}
.modal-head{ display:flex; align-items:center; justify-content:space-between; margin-bottom:10px; }
.modal-title{ font-size:18px; font-weight:700; }
.modal-close{ border:none; background:transparent; font-size:18px; cursor:pointer; }

.modal-grid{ display:grid; gap:10px; margin-top:6px; }
.modal-row{ display:grid; grid-template-columns: 180px 1fr; gap:10px; align-items:center; }
.modal-row input{
  padding:10px 12px; border-radius:10px; border:1px solid #d9dee3; outline:none;
}
.modal-row span{ font-weight:600; }

.modal-actions{ display:flex; gap:10px; margin-top:14px; flex-wrap:wrap; }
/* Status-palkki videon päällä, aina näkyvissä */
.status-bar{
  position:absolute;
  top:8px;
  left:50%;
  transform:translateX(-50%);
  z-index:5;
  color:#fff;
  font-size:12px;
  font-weight:700;
  padding:4px 12px;
  border-radius:999px;
  box-shadow:0 2px 8px rgba(0,0,0,.15);
  background:rgba(0,0,0,.7); /* fallback */
}

/* värit tilan mukaan */
.status-bar.status-live  { background:#27ae60; }
.status-bar.status-meds  { background:#e74c3c; }
.status-bar.status-drink { background:#3498db; }

/* Varmistus että video ei peitä header/tilaa */
.phone-frame { position:relative; }
.profile-pic { display:block; position:relative; z-index:1; }
.phone-header { position:relative; z-index:2; margin-bottom:8px; }
